import LoginApi from "@/api/LoginApi.js"

const state = {
    loading: {
        btn: false,
        img: false,
    },
    axiosFormData: {
        intention: ""
    },
    captcha: {},
}

const getters = {
    captchaImg(state) {
        return `data:${state.captcha.image_type};${state.captcha.image_decode},${state.captcha.captcha_image}`
    }
}

const actions = {
    getFormData({ commit }) {
        let getIntention = () => {
            return LoginApi.getIntention().then(res => {
                commit("setAxiosFormData", { type: "intention", value: res })
            });
        }

        return Promise.all([getIntention()])
    },
    getCaptcha({ commit }) {
        commit("setLoading", { type: "img", value: true })
        return LoginApi.getCaptcha().then(res => {
            commit("setCaptcha", res)
            commit("setLoading", { type: "img", value: false })
        });
    }
}

const mutations = {
    setLoading(state, data) {
        state.loading[data.type] = data.value
    },
    setAxiosFormData(state, res) {
        state.axiosFormData[res.type] = res.value
    },
    setCaptcha(state, captcha) {
        state.captcha = captcha
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}