import axios from "@/axios.js"

export default {
    //意向列表
    getIntention(passVal) {
        return axios.get('api/customer/get_intention', {
            params: passVal
        })
    },
    // 提交表单
    submit(passVal) {
        return axios.post('api/customer', passVal)
    },
    // 获取图片验证码
    getCaptcha(passVal){
        return axios.post('api/captcha', passVal)
    }
}