import Vue from 'vue'
import VueRouter from 'vue-router'

import Homepage from '@/views/Homepage.vue'
const Product = () => import('@/views/Product.vue')
const Traderslink = () => import('@/views/Product/Traderslink.vue')
const ChannelCooperation = () => import('@/views/ChannelCooperation.vue')
const AboutUs = () => import('@/views/AboutUs.vue')
const Login = () => import('@/views/Login.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Homepage',
    component: Homepage
  },
  // {
  //   path: '*',
  //   redirect: '/Homepage'
  // },
  {
    path: '/Homepage',
    name: 'Homepage',
    component: Homepage
  },
  {
    path: '/Product',
    name: 'Product',
    component: Product,
    children: [
      {
        path: "Traderslink",
        name: 'Traderslink',
        component: Traderslink,
      }
    ]
  },
  // {
  //   path: '/CustomerCase',
  //   name: 'CustomerCase',
  //   component: 
  // },
  {
    path: '/ChannelCooperation',
    name: 'ChannelCooperation',
    component: ChannelCooperation,
  },
  {
    path: '/AboutUs',
    name: 'AboutUs',
    component: AboutUs,
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
